@import "../../../style/theme/default/var.css";

@component-namespace template {
  @b page {
    .el-tabs__content {
      padding-bottom: 0!important;
    }
  }
  @b layout-left {
    float:left;
    width: 600px;
    height: auto;
    padding: var(--theme-padding-base);
    background-color: var(--theme-background-color-primary);
    .activity-wrap {
      .el-calendar__header{
        .tab-item{
          height:30px;
          line-height: 28px;
          font-weight: normal;
        }
        .el-calendar__icon-btn{
          height: 30px;
        }
      }
      .el-calendar__content  tbody>tr:first-child {
        background-color: var(--theme-background-color-primary);
      }
      .el-calendar-table td.current:not(.disabled) .el-calendar__date {
        .tmp-festival.is-active .el-button {
          display: block;
          width: 100%;
        }
      }
/*      .el-calendar-table td.current:not(.disabled):hover .el-calendar__date,
      .el-calendar-table td:hover .el-calendar__date{
        background-color: var(--theme-color-primary) !important;
        color: var(--color-white);
          & .date-almanac,
          & .el-calendar__date-value{
            color: var(--color-white) !important;
          }
      } */
      & .el-calendar__date{
        height: 80px;
        padding: var(--theme-padding-small);
          &:hover{
             .tmp-festival .el-button {
               display: block;
               width: 100%;
               background: var(--theme-color-primary);
               .delete-btn {
                 display: block;
               }
             }
           }
         & .el-calendar__date-value {
            .date-almanac {
              display: inline-block;
              width: 80%;
              text-align: right;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
            }
           }
         & .el-calendar__date-content {
            overflow-y: inherit;
            text-align: center;
            height: 50px;
          .total {
            cursor: default;
            position: absolute;
            top: -45px;
            margin-left: -12px;
            background-color: var(--theme-color-primary);
            color: var(--color-white);
            border-radius: 100%;
            font-size: 16px;
            display: inline-block;
            min-width: 26px;
            height: 26px;
            text-align: center;
            line-height: 26px;
            margin-top: var(--theme-margin-base);
          }
         }
      }
    }
    .activity-wrap__header {
      margin-top: 0;
      line-height: 30px;
      font-size: 14px;
      font-weight: bold;
    }
    .activity-wrap__record {
      background: var(--color-white);
      & .el-col{
        border:1px solid var(--theme-border-color-primary);
        border-right: 0;
        padding: var(--theme-padding-small);
        &:last-child{
           border-right: 1px solid var(--theme-border-color-primary);
        }
        & .record-name{
          font-size: var(--theme-font-size-small);
          margin-bottom: var(--theme-margin-base);
        }
        & .record-num{
          font-size: var(--theme-font-size-small);
          b{
            font-size: var(--theme-font-size-middle);
          }
        }
      }
    }
  }
  @b layout-right {
    margin-left: 610px!important;
  }
}
@component-namespace movable{
  @b shaft {
    position: relative;
    margin: 0;
    padding: var(--theme-padding-base);
    >li {
      &:before {
         content: ' ';
         position: absolute;
         top: 20px;
         left: 0;
         height: 100%;
         border-left: 1px solid var(--theme-border-color-primary);
      }
    }
    li {
      position: relative;
      padding-left: var(--theme-padding-large);
      margin-bottom: var(--theme-margin-base);
      .movable-icon {
        cursor: pointer;
        position: absolute;
        top: 7px;
        left: -7px;
        display: inline-block;
        width: 16px;
        height: 16px;
        background-color: var(--theme-color-primary);
        border-radius: 50%;
        overflow: hidden;
        transition: all 0.5s ease;
        &:before {
          content: ' ';
          position: absolute;
          top: 6px;
          left: 5px;
          display: inline-block;
          width: 5px;
          height: 5px;
          border-top: 1px solid var(--color-white);
          border-right: 1px solid var(--color-white);
          transform: rotate(-45deg);
          -webkit-transform: rotate(-45deg);
        }
        &.active {
           transform: rotate(180deg);
           -webkit-transform: rotate(180deg);
         }
      }
      .movable-title {
        position: relative;
        display: inline-block;
        padding: 6px var(--theme-padding-base);
        color: var(--color-white);
        background-color: var(--theme-color-primary);
        border-radius: 2px;
        line-height: 18px;
        + .movable-title {
          margin-left: 10px;
          }
        &:before {
          position: absolute;
          left: -10px;
          top: 10px;
          display: inline-block;
          content: "";
          width: 0;
          height: 0;
          border: 5px solid var(--theme-color-primary);
          border-color: transparent var(--theme-color-primary) transparent transparent;
        }
      }
      i.bui-add {
        cursor: pointer;
        padding: 0 var(--theme-padding-small);
        color: var(--theme-color-primary);
      }
      li {
        position: relative;
        padding: var(--theme-padding-small) var(--theme-padding-base);
        margin: var(--theme-margin-base) 0;
        border: 1px solid var(--theme-border-color-primary);
        border-radius: 2px;
        &:before {
          position: absolute;
          left: -5px;
          top: 50%;
          display: inline-block;
          content: "";
          width: 5px;
          height: 5px;
          margin-top: -2.5px;
          border-top: 1px solid var(--theme-border-color-primary);
          border-right: 1px solid var(--theme-border-color-primary);
          background-color: var(--color-white);
          transform: rotate(-135deg);
          -webkit-transform: rotate(-135deg);
        }
        &:hover {
          background-color: var(--theme-background-color-primary);
          &:before {
            background-color: var(--theme-background-color-primary);
          }
        }
        &:after {
          content: ' ';
          position: absolute;
          top: 50%;
          left: -25px;
          display: inline-block;
          width: 10px;
          height: 10px;
          margin-top: -5px;
          background-color: var(--theme-border-color-primary);
          border-radius: 50%;
          overflow: hidden;
        }
        &.is-end {
          .movable-content,
          .el-tag,
          .control-btn i{
            color: var(--theme-font-color-secondary);
          }
          .control-btn i{
            cursor: default;
          }
        }
        .movable-content {
          display: -webkit-box;
          max-height: 52px;
          margin-right: var(--theme-margin-large);
          /* autoprefixer: off*/
          -webkit-box-orient: vertical;
          /* autoprefixer: on*/
          -webkit-line-clamp: 2;
          overflow: hidden;
          line-height: 26px;
        }
        .tag-box {
          margin-right: 50px;
          .el-tag {
            position: relative;
            padding: 0 10px;
            margin: 2px;
            background-color: var(--color-white);
            width: 94px;
            @utils-ellipsis;
            &:hover {
               .el-tag__close {
                 display:inline-block;
               }
             }
              .el-tag__close{
                display:none;
                top: 2px;
                right: -3px;
                position: absolute;
                -ms-transform: scale(.6,.6);
                transform: scale(.6,.6);
                color: var(--color-white);
                background-color:var(--theme-color-primary);
              }
          }
        }
        .el-row {
          position: relative;
          .control-btn {
            display: inline-block;
            position: absolute;
            top: 50%;
            right: 0;
            margin-top: -12.5px;
            i {
              cursor: pointer;
              font-size: 18px;
              color: var(--theme-color-primary);
            }
            i + i{
              margin-left: var(--theme-margin-small);
            }
          }
        }
      }
    }
    * {
      margin: 0;
      padding: 0;
      list-style-type: none;
    }

  }
}

.tmp-festival {
  margin-left: -6px;
  margin-right: -6px;
  span {
    display: block;
    position: relative;
  }
  .el-button {
    position: relative;
    background: var(--theme-color-primary);
    color: var(--color-white);
    margin-top: 31px;
    margin-left: 0;
    border: none;
    border-radius: 0;
    display: none;
    &.active {
      display: block;
      width: 100%;
       background: transparent;
      .delete-btn {
        display: none;
      }
     }
  }
}

@media (max-width: 1366px) {
  .template-layout-left .activity-wrap .el-calendar__date{
    height: 70px;
  }
  .template-layout-left .activity-wrap .el-calendar__date .el-calendar__date-content {
    height: 30px;
    line-height: 30px;
  }
  .template-layout-left .activity-wrap .el-calendar__date .el-calendar__date-content .total {
    min-width: 24px;
    font-size: 13px;
    height: 24px;
    line-height: 24px;
    top: -39px;
  }

  .el-calendar__date .tmp-festival .el-button {
    /*margin-top: 4px;*/
    margin-top: 27px;
    padding: 3px 5px;
  }
  .template-layout-left .activity-wrap__record .el-col .record-name {
    margin-bottom: var(--theme-margin-small);
  }
}



